import React, { Component } from "react";
import "./styles/input.css";
import Print from "./assets/print.svg";
import Copy from "./assets/copy.svg";

export class Input extends Component {
  constructor(props) {
    super(props);
    this.state = {
      valueFiller: "",
      valueCounter: "",
    };
  }

  handleFillerChange = (event) => {
    this.setState({ valueFiller: event.target.value });
  };

  handleCounterChange = (event) => {
    this.setState({ valueCounter: event.target.value });
  };

  render() {
    return (
      <React.Fragment>
        <div className="input">
          <input
            placeholder="$ - for counter"
            value={this.state.valueFiller}
            onChange={this.handleFillerChange}
          ></input>
          <input
            placeholder="How many to print?"
            value={this.state.valueCounter}
            onChange={this.handleCounterChange}
          ></input>

          <div className="buttons">
            <button
              className="btn print"
              onClick={() => this.props.onTextFill({
                filler: this.state.valueFiller,
                counter: this.state.valueCounter,
              })}
            >
              <img src={Print} alt="print button"></img>Print
            </button>

            <button className="btn copy" onClick={() => this.props.onCopy()}>
              <img src={Copy} alt="copy button"></img>Copy
            </button>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
