import React, { Component } from "react";
import "./App.css";
import { Navbar } from "./components/navbar";
import { Input } from "./components/input";
import { Textarea } from "./components/textarea";

class App extends Component {
  state = {
    textToPrint: [],
    rawText: "",
  };

  handleTextFilling = ({ filler, counter }) => {
    let textToRender = [];
    let rawTextToCopy = "";
    for (let i = 1; i <= counter; i++) {
      let tmpText = `${filler}\n`.replace("$", i)
      rawTextToCopy += tmpText;
      textToRender[i - 1] = <p key={i}>{tmpText}</p>;
    }

    this.setState({ textToPrint: textToRender });
    this.setState({ rawText: rawTextToCopy });
  };

  handleCopy = () => {
    var tempInput = document.createElement("textarea");
    tempInput.style = "position: absolute; left: -1000px; top: -1000px; font-size: 8px;";
    tempInput.value = this.state.rawText;
    document.body.appendChild(tempInput);
    tempInput.select();
    document.execCommand("copy");
    document.body.removeChild(tempInput);
  };

  render() {
    return (
      <React.Fragment>
        <Navbar></Navbar>
        <main>
          <Input
            onTextFill={this.handleTextFilling}
            onCopy={this.handleCopy}
          ></Input>
          <Textarea>{this.state.textToPrint}</Textarea>
        </main>
      </React.Fragment>
    );
  }
}

export default App;
